omo objetivo ayudar a los usuarios a resolver problemas específicos. La atención se brinda por teléfono, internet y, en algunos casos, visitas del personal a la casa del cliente. Puede ayudar a resolver errores de programación de software o fallas de instalación de hardware de forma remota. En general, es importante señalar que la atención que brinda el soporte técnico no suele incluir la capacitación o educación de los usuarios. Esto significa que, si llama al soporte técnico porque no sabe cómo usar el servicio, lo más probable es que el personal de soporte lo redirija a otra ubicación y no lo capacite. Por este motivo, muchas empresas cuentan con dos áreas diferenciadas de soporte técnico y capacitación.")])]),_c('div',{staticClass:"tarjeta tarj-bg_2 p-4 tb_2",attrs:{"titulo":"Call center"}},[_c('p',{staticClass:"mb-0"},[_vm._v("En algunos casos, la atención al cliente requiere más contacto presencial, por lo que la mesa de ayuda también cuenta con una línea telefónica para atender las consultas y reclamos de los clientes de manera personal y profesional.")])]),_c('div',{staticClass:"tarjeta tarj-bg_2 p-4 tb_3",attrs:{"titulo":"Blog"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Es la mesa de ayuda que también sirve como base de conocimientos, en la cual, los clientes pueden encontrar artículos relacionados con las preguntas más frecuentes; Así no se tendrá que consultar por otros medios y se ahorrará tiempo a los empleados.")])]),_c('div',{staticClass:"tarjeta tarj-bg_2 p-4 tb_4",attrs:{"titulo":"Comunidad"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Las comunidades de mesa de ayuda permiten a los usuarios interactuar o leer a otros clientes de la marca que tienen las mismas dudas o contratiempos que ellos. Las respuestas dadas por los mismos usuarios serán útiles para otros. Siempre que esta información provenga de la experiencia con el producto, es bastante confiable.")])])])],1)]),_vm._m(7)])],1)],1)}
